export default {
	data() {
		return {
			typeCheck:0,//0登录 1注册
			cooldownTime:'获取验证码',
			btnDisable:false,
			second:'',
			logUserName:'',//登陆用户名
			logPassword:'',//登陆密码
			registerUserName:'',//注册用户名
			registerPassword:'',//注册密码
			registerPasswordSecond:'',//注册密码重复
			verificationCodeValue:''//验证码
		}
	},
	onLoad() {
		let THAT = this;
		THAT.getLoginStorage();//取本地账号密码
	},
	onShow() {
		console.log("onShow")
	},
	methods: {
		chooseLogin(){
			this.typeCheck=0;
		},
		chooseRegister(){
			this.typeCheck=1;
		},
		toRetrievePassword(){
			uni.navigateTo({
				url:'RetrievePassword',
				success() {
					console.log("success toRetrievePassword");
				},
				fail(){
					console.log("fail toRetrievePassword");
				}
			})
		},
		toGender(){
			if(!this.registerUserName){
				uni.showToast({
					title:"请输入手机号",
					icon:"none"
				})
			}
			else if(!this.verificationCodeValue){
				uni.showToast({
					title:"请输入验证码",
					icon:"none"
				})
			}
			else if(!(this.registerPassword===this.registerPasswordSecond)){
				uni.showToast({
					title:"两次输入密码不一致",
					icon:"none"
				})
			}
			// else if(){ //验证码错误
			// 	uni.showToast({
			// 	title:"验证码错误",
			// 	icon:"none"
			// 	})
			// }
			else{
				console.log(`账号:${this.registerUserName} 密码:${this.registerPassword} 密码重复:${this.registerPasswordSecond} 验证码:${this.verificationCodeValue}`)
				uni.navigateTo({//跳转Gender
					url:'../setBasicInfor/Gender',
					success() {
						console.log("success toGender");
					},
					fail(){
						console.log("fail toGender");
					}
				})
			}
			
		},
		toHome(){
			if(true){//登陆条件判断
				var That = this;
				console.log(`账号:${this.logUserName} 密码:${this.logPassword}`);
				uni.setStorage({//本地缓存账号
					key:"UserName",
					data:That.logUserName
				});
				uni.setStorage({//本地缓存密码
					key:"Password",
					data:That.logPassword
				});
				uni.reLaunch({
					url: '../home/Home',
					success() {
						console.log("success toHome");
					},
					fail(){
						console.log("fail toHome");
					}
				});
			}else{
				uni.showToast({
					title:"用户名或密码错误",
					icon:"none"
				})
			}		
		},
		verificationCode(){//验证码函数
			var THAT = this;
			if(this.registerUserName){
				//这里写发送验证码函数
				uni.showToast({
					title:"验证码已发送"
				})
				//倒计时函数
				this.second = 's';
				this.cooldownTime = 60;
				this.btnDisable = true;
				var verTime = setInterval(function(){
					THAT.cooldownTime = THAT.cooldownTime - 1;
					if(THAT.cooldownTime==-1){
						THAT.btnDisable = false;
						THAT.cooldownTime='获取验证码';
						THAT.second='';
						clearInterval(verTime);
					}
				},1000)
			}
			else{
				uni.showToast({
					title:"请输入手机号",
					icon:"none"
				})
			}
		},
		getLoginStorage(){//取本地缓存的登陆账号密码
			var That = this;
			uni.getStorage({
				key:"UserName",
				success(e){
					That.logUserName=e.data;
					console.log(That.logUserName);
				}
			})
			uni.getStorage({
				key:"Password",
				success(e){
					That.logPassword=e.data;
					console.log(That.logPassword);
				}
			})
		},
		//右上角分享功能
		onShareAppMessage: function (res) {
			var that = this;
			return {
				title: '',
				path: '/pages/home/Home',
				imageUrl: "",
				success: function (res) {
					console.log(res);
				},
				fail: function (res) {
					console.log(res);
				}
			}
		},
	}
}
